@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;0,1000;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900;1,1000&family=Ruge+Boogie&display=swap');

$max-width: 1000px;
$default-color: #2c3e50;
$gray-bg: #f7f7fa;

body {
  margin: 0;
  padding: 0 1rem;
  font-family: 'Nunito', sans-serif;
  font-size: 1rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  max-width: $max-width;
  margin: 0 auto;
}

.AppHeader {
  display: flex;
  justify-content: space-between;
  margin: 1rem 0;

  h1 {
    margin: 0;
    padding: 0;
  }

  #logo {
    width: 56px;
    height: 56px;
    margin-right: 0.5rem;
  }

  .header-main {
    display: flex;
    align-items: center;
  }

  .header-main h1 {
    font-family: 'Ruge Boogie', cursive;
  }
}

.run-calendar {
  .fc-day {
    &.fc-day-past {
      color: $default-color;
      background: $gray-bg;
      opacity: 0.5;
    }

    .fc-event {
      background: transparent;
      border: none;
      padding: 0.5rem;
      white-space: normal;
      font-weight: bold;

      .fc-event-main {
        color: $default-color;
      }
    }
  }
}

.AppFooter {
  padding: 1rem;
  text-align: center;
}